import { Injectable } from "@angular/core";
import { Customer } from "./interface/customerInferface";
import { v4 as uuidv4 } from 'uuid';
import { AlertController } from "@ionic/angular";
import { ListRegistrationsComponent } from "./listRegistrations/listRegistrations.component";

@Injectable({
    providedIn: 'root'
})

export class CustomerService {

    customers: Customer[] = []
    public userIdHuntingService: number;

    username;
    greenSmile;
    redSmile;
    id;

    constructor(
    ) {}

    getAllCustomers() {
        return [...this.customers];
    }

    saveCustomer(customer: Customer) {
        const uuid = uuidv4();
        customer.id = uuid;
        this.customers.push(customer);
    }

    getCustomer(customerId: number) {
        return {... this.customers.find(customer => {
            return customer.id === customerId;
        })};
    }

    getRedSmileys(customerId: number) {
        return  {... this.customers.find(customer => {
            console.log(customer.redSmile)
            return customer.id === customerId;
        })}.redSmile;
    }

    setRedSmileys(smileys: number) {
        this.redSmile = smileys;
    }

    getGreenSmileys(customerId: number) {
        return  {... this.customers.find(customer => {
            console.log(customer.greenSmile)
            return customer.id === customerId;
        })}.greenSmile;
    }

    setGreenSmileys(smileys: number) {
        this.greenSmile = smileys;
    }

    deleteCusomer(customerId: number){
        this.customers.forEach((value,index)=>{
            if(value.id==customerId) this.customers.splice(index,1);
         }); 
    };

    updateCustomer(customerId: number){
        console.log(this.redSmile);
        console.log(this.greenSmile);
        this.customers.find(customer => customer.id == customerId).greenSmile = this.greenSmile;
        this.customers.find(customer => customer.id == customerId).redSmile = this.redSmile;
        console.log(this.customers.find(customer => customer.id == customerId).greenSmile = this.greenSmile);
        console.log(this.customers.find(customer => customer.id == customerId).redSmile = this.redSmile);
    }
}