import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import { CustomerService } from '../customer.service';
import { ListRegistrationsComponent } from '../listRegistrations/listRegistrations.component';
import { NewsignupComponent } from '../newsignup/newsignup.component';

@Component({
  selector: 'app-popover',
  templateUrl: './popover.component.html',
  styleUrls: ['./popover.component.css']
})
export class PopoverComponent implements OnInit {

  @Input() id: number;

  constructor(
    private customerservice: CustomerService,
    private alertController: AlertController,
  ) { }

  ngOnInit() {
  }

  onDeleteCustomer(){
    this.alertController.create({
        header: 'Teilnehmer entfernen',
        message: 'Wollen Sie den Teilnehmer wirklich entfernen?',
        buttons: [
            {
                text: 'Ja',
                handler: () => {
                  console.log(this.id);
                  this.customerservice.deleteCusomer(this.id);
                  this.reloadComponent();
                }
            },
            {
                text: 'Nein',
                role: 'cancel',
            },
        ]
    }).then(alertEl => {
        alertEl.present();
    });
  }

  reloadComponent() {
        console.log("reload")
    }
}
